@import url('https://cdnjs.cloudflare.com/ajax/libs/skeleton/2.0.4/skeleton.min.css');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background-color: #292929;
    color: #fff;
    font-family: 'Roboto', sans-serif;
}

.contenido {
    padding:20px;
}

.contenido-principal {
    background-color: #404040;
    padding: 30px;
    border-radius: 20px;
}

h2 {
    font-family: 'Raleway', sans-serif;
    font-weight: 100;
    color:#85C6FF;
    margin-bottom:30px;
    font-size:3rem!important;
    text-align: center;
} 





.alert p {
    margin-bottom: 0!important;
}

header {
    margin: 30px 0;
}


.presupuesto p{
    margin-bottom: 0;
    font-weight: bold;
}
.presupuesto p span {
    font-weight: normal;
}
#gastos {
    margin-bottom: 30px;
}
h1 {
    font-family: 'Raleway', sans-serif;
    font-weight: 100;
    color: white;
    text-align: center;
    font-size: 4.8rem!important;
}

