.alert {
    position: relative;
    padding: .75rem 1.25rem;
    margin-bottom: 1rem;
    border: 1px solid transparent;
    border-radius: .25rem;
} 
.alert-primary {
    color: #004085;
    background-color: #cce5ff;
    border-color: #b8daff;
}
.alert-success {
    color: #155724;
    background-color: #d4edda;
    border-color: #c3e6cb;
}
.alert-danger {
    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6cb;
} 
.alert-warning {
    color: #856404;
    background-color: #fff3cd;
    border-color: #ffeeba;
}
.gastos-realizados  {
    margin-bottom: 2rem;
}
.gastos-realizados li{
    list-style: none;
    margin-bottom: 0rem;
    border-bottom: 1px solid #e1e1e1;
    padding: 1rem 0;
}
.gastos-realizados li:last-of-type {
    margin-bottom: 0!important;
}
.gastos-realizados li p {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin:0;
}
.gastos-realizados li p .gasto {
    background-color: #004085;
    padding: 1rem;
    color:white;
}